<template>
  <header>
    <h1 class="title-1th title">{{ jsonData.name }}</h1>
    <ul class="title-2th">
      <li v-for="ocupation in jsonData.ocupations" :key="ocupation.id">
        {{ ocupation.label }}
      </li>
    </ul>
    <ul class="title-3th">
      <li v-for="contactInfo in jsonData.contacts" :key="contactInfo.id">
        <contact :data="contactInfo" />
      </li>
    </ul>
  </header>
  <section>
    <h3 class="uk-card-title">
      <span uk-icon="icon: laptop; ratio: 1.5"></span> Work & Education
    </h3>
    <cv-section
      v-for="job in jsonData.jobs"
      :key="job.id"
      :data="job"
    ></cv-section>
    <cv-section
      v-for="education in jsonData.educations"
      :key="education.id"
      :data="education"
    ></cv-section>
  </section>
  <section>
    <h3 class="uk-card-title">
      <span uk-icon="icon: laptop; ratio: 1.5"></span> Skills
    </h3>
    <div class="skills-container">
      <span
        v-for="skill in jsonData.skills"
        :key="`skill-${skill}`"
        class="uk-badge soft-skill"
        >{{ skill }}</span
      >
    </div>
  </section>
</template>

<script>
import contact from "../components/cvComponents/contact";
import CvSection from "../components/cvComponents/CvSection";
// import mokeData from "./../mokeData/cvData.json";
import axios from "axios";

export default {
  components: { contact, CvSection },
  name: "Cv",
  data() {
    return {
      jsonData: {}
    };
  },
  methods: {
    getJsonFromAxios() {
      axios
        .get(
          "https://raw.githubusercontent.com/vladap99/json-infos/main/cvData.json"
        )
        .then(response => (this.jsonData = response.data))
        .catch(err => console.warn(err));
    }
  },
  created() {
    this.getJsonFromAxios();
  }
};
</script>

<style lang="less">
.tm-sidebar-left {
  display: none;
}

:root {
  --custom-orange: #f05a28;
  --custom-gray: rgb(102, 102, 102);
}

body {
  padding: 100px;
  width: 1200px;
  height: auto;
  margin: 0 auto;
}

a {
  color: inherit;
}

a:hover {
  color: inherit;
}

header {
  text-align: center;
}

header ul {
  display: flex;
  justify-content: center;
  list-style-type: none;
}

header ul.title-2th {
  color: var(--custom-orange);
  list-style-type: initial;
}

header ul.title-2th li {
  margin: 0 20px;
}

header ul.title-2th li:first-child {
  margin: 0 20px;
  list-style-type: none;
}

header ul.title-3th .uk-icon {
  margin-right: 5px;
}

header ul.title-3th li {
  margin: 0 5px;
  text-decoration: none;
  padding-right: 10px;
  border-right: solid 2px rgb(102, 102, 102);
}

header ul.title-3th li:last-child {
  border-right: none;
}

.uk-article-meta {
  float: right;
}

.company {
  color: var(--custom-orange);
  padding-left: 50px;
}

.skills-main-container {
  display: flex;
  justify-content: center;
}

.skills-container {
  text-align: center;
  margin: 0 50px;
}

.skills-container .soft-skill {
  background-color: var(--custom-orange);
  margin: 2px;
  padding: 12px 15px 15px 15px;
}
</style>
